import { Component, Output, OnChanges, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent{
  isShowed = false;
  player: {nomePersonagem: string, nomeJogador: string } = {nomePersonagem: '', nomeJogador: ''};
  Random(personData: any) {
    this.isShowed = personData.radomize;
    this.player.nomeJogador = personData.nomeJogador;
    this.player.nomePersonagem = personData.nomePersonagem;
  }
}
